<script type="text/ecmascript-6">
import Location from 'components/location/location'
import Star from 'components/star/star'
import { formatDate } from 'common/js/util'
import {
  /* eslint-disable no-unused-vars */
  createAPI
} from 'cube-ui'
export default {
  data () {
    return {
      beginDateTime: '',
      endDateTime: '',
      scrollOptions: {
        scrollbar: false
      },
      isNear: false,
      isAi: false,
      isCond: false,
      serveCheck: ['2'],
      serveOptions: [
        {
          label: '猫猫',
          value: '1'
        },
        {
          label: '狗狗',
          value: '2'
        }
      ],
      sizeCheck: ['2'],
      sizeOptions: [
        {
          label: '小',
          value: '1'
        },
        {
          label: '中',
          value: '2'
        },
        {
          label: '大',
          value: '3'
        },
        {
          label: '超大',
          value: '4'
        }
      ]
    }
  },
  computed: {
    begin () {
      return this.beginDateTime ? formatDate(this.beginDateTime, 'MM/dd/hh:mm') : '--'
    },
    end () {
      return this.endDateTime ? formatDate(this.endDateTime, 'MM/dd/hh:mm') : '--'
    },
    totalDays () {
      if (this.beginDateTime && this.endDateTime) {
        let delta = this.endDateTime - this.beginDateTime
        delta = delta > 0 ? delta / (1000 * 60 * 60 * 24) : 0
        return Math.ceil(delta)
      }
      return 0
    },
    nearCls() {
      return this.isNear ? 'icon-arrow-up' : 'icon-arrow-down'
    },
    aiCls() {
      return this.isAi ? 'icon-arrow-up' : 'icon-arrow-down'
    },
    condCls() {
      return this.isCond ? 'icon-arrow-up' : 'icon-arrow-down'
    },
    showMask() {
      return this.isNear || this.isAi || this.isCond
    }
  },
  methods: {
    showNear() {
      this.isNear = !this.isNear
      this.isAi = this.isCond = false
    },
    showAi() {
      this.isAi = !this.isAi
      this.isNear = this.isCond = false
    },
    showCondition() {
      this.isCond = !this.isCond
      this.isNear = this.isAi = false
    },
    hideMask() {
      this.isNear = this.isAi = this.isCond = false
    },
    search() {
      this.isCond = false
    },
    selectBeginDate() {
      if (!this.beginDatePicker) {
        this.beginDatePicker = this.$createDatePicker({
          title: '开始时间',
          min: new Date(2008, 7, 8, 8, 0, 0),
          max: new Date(2020, 9, 20, 20, 59, 59),
          value: new Date(),
          format: {
            year: 'YY年',
            month: 'M月',
            date: 'D日'

          },
          columnCount: 5,
          onSelect: this.selectBeginHandle
        })
      }
      this.beginDatePicker.show()
    },
    selectEndDate() {
      if (!this.endDatePicker) {
        this.endDatePicker = this.$createDatePicker({
          title: '结束时间',
          min: new Date(2008, 7, 8, 8, 0, 0),
          max: new Date(2020, 9, 20, 20, 59, 59),
          value: new Date(),
          format: {
            year: 'YY年'
          },
          columnCount: 5,
          onSelect: this.selectEndHandle
        })
      }
      this.endDatePicker.show()
    },
    selectBeginHandle (date, selectedVal, selectedText) {
      this.beginDateTime = date
    },
    selectEndHandle (date, selectedVal, selectedText) {
      this.endDateTime = date
    },
    selectSeller() {
      this.$router.push({
        path: `/seller`
      })
    }
  },
  components: {
    Location,
    Star
  }
}
</script>
